@import "./variables/variables";

.catalog {
  display: flex;
  flex-wrap: wrap;

  max-width: 944px;
  margin: 0 auto;
  padding: 50px 0;

  &__card {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;

    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: $margin-catalog;
    transition: all 300ms ease-in-out;

    &:not(:nth-child(4n)) {
      margin-right: $margin-catalog;
    }
    &:hover {
      transform: scale(1.2);
      color: $card-text-color;
    }
  }
}
